import React from "react";
import { Row, Col, Icon } from "antd";
import Navigation from "./Navigation";
import './Header.css';

const Header = props => {
  return (
    <header className="herofull">
      <Navigation />
      <Row type="flex" align="middle">
        <Col span={24}>
          <p className="hero-intro">Hey there! My name is</p>
          <p className="hero-name">Kostiantyn Borodach.</p>
          <p className="hero-subtitle">
            I'm a Creative Problem Solver and Full Stack Developer.
          </p>
          <a
            href="https://www.linkedin.com/in/kostiantyn-borodach-4a3859152/"
            aria-label="My Linkedin"
            target="_blank"
            rel="noopener noreferrer"
            className="contact-link"
          >
            <Icon
              type="linkedin"
              style={{ fontSize: 50 }}
              className="hero-icon"
            />
            <span className="contact-text"> Linkedin</span>
          </a>
          <span className="hero-dot">·</span>
          <a
            href="https://github.com/Kostiantyn777"
            aria-label="My Github"
            target="_blank"
            rel="noopener noreferrer"
            className="contact-link"
          >
            <Icon
              type="github"
              style={{ fontSize: 50 }}
              className="hero-icon"
            />
            <span className="contact-text"> Github</span>
          </a>
          <div className="hero-arrow">
            <a href="#portfolio">﹀</a>
          </div>
        </Col>
      </Row>
    </header>
  );
};

export default Header;
